@media screen and (min-width: 960px) {
    .desktop {
        display: inline;
        display: initial;
    }
    .mobile {
        display: none;
    }
}

.anchor-btn {
    border: none;
    border-radius: 0;
    padding: 0;
    cursor: pointer;
    background-color: transparent;
}
:root {
    --font-h: 'Nunito', serif;
    --font-p: 'Raleway', sans-serif;
    --primary: #0dad62;
    --primary-light: #61c797;
    --primary-xlight: #bdead5;
    --primary-dark: #01582f;
    --secondary: #ff4081;
    --secondary-light: #ff79b0;
    --secondary-xlight: #ffdae9;
    --secondary-dark: #c60055;
    --text-primary: #0e0e0e;
    --text-secondary: #0e0e0e;
    --almost-white: rgb(236, 231, 231);
    --almost-black: rgb(22, 21, 21);
    --soft-black: #5C5C5C;
}

/* MODAL STYLES */

.ReactModal__Body--open {
    overflow: hidden;
}

.modal {
    position: absolute;
    top: 40px;
    left: 40px;
    right: 40px;
    bottom: 40px;
    border: none;
    background: rgb(255, 255, 255);
    overflow: auto;
    overflow-x: hidden;
    border-radius: 4px;
    outline: none;
    background-color: transparent;
    color: rgb(22, 21, 21);
    color: var(--almost-black);
}

.btnCloseModal {
    border: none;
    margin-left: 0.5rem;
    font-size: 1.5rem;
    cursor: pointer;
    padding-top: 0.2rem;
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
    background-color: #ff4081;
    background-color: var(--secondary);
    visibility:visible;
}

.btnCloseModal:hover {
    background-color: #c60055;
    background-color: var(--secondary-dark);
    color: rgb(236, 231, 231);
    color: var(--almost-white);
}

.tabContainer {
    height: 3rem;
    background-color: transparent;
}

.tabBtn {
    height: 100%;
    border: none;
    padding: 1rem;
    font-size: 1rem;
    text-transform: uppercase;
    cursor: pointer;
    background-color: #61c797;
    background-color: var(--primary-light);
}

.activeTab {
    background-color: #bdead5;
    background-color: var(--primary-xlight);
}

.tabBtn:focus {
    outline: none;
}

.lastTabBtn {
    border-radius: 0 4px 0 0;
}

.contentWrapper {
    background-color: #bdead5;
    background-color: var(--primary-xlight);
    height: auto;
}

.contentContainer {
    padding: 2rem;
}

@media (min-width: 720px) {
    .modal {
        max-width: 720px;
        margin: auto;
    }
}

/* DETAIL STYLES */

#detailContainer {
    margin: 0;
}

#detailImgWrapper img {
    max-width: 100%;
}

#detailTxtContent {
    width: 85%;
}

#detailTxtExtra {
    line-height: 1.3rem;
    margin-bottom: 1.5rem;
}

.key {
    color: rgb(134, 134, 134);
}

/* .loveBtnWrapper {
    width: auto;
    height: auto;
} */

.loveBtn {
    z-index: 1!important;
    font-size: 1.5rem!important;
    border-radius: 3px;
    text-transform: uppercase;
    background-color: #ff4081;
    background-color: var(--secondary);
    color: white;
    cursor: pointer;
    padding: 0.5rem 0.8rem 0.5rem 0.8rem;
    position: relative;
}

.loveBtn:hover {
    background-color: #c60055;
    background-color: var(--secondary-dark);
}

.loveBtn:focus {
    outline: none;
}

.heart{
    position: relative;
    color: #ff4081;
    color: var(--secondary);
    /* animation-name: showLove; */
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
}

.heart1 {
    font-size: 2rem;
    left: -5.5rem;
    top: 0.1rem;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}

.heart2 {
    font-size: 2.5rem;
    left: -4.6rem;
    top: 0.5rem;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}

.heart3 {
    font-size: 1.7rem;
    left: -8rem;
    top: 0rem;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    
}

@-webkit-keyframes showLove {
    0% {
        opacity: 1.0;
    }
    50% {
        opacity: 1.0;
    }
    100% {
        opacity: 0.0;
        top: -3rem;
    }
}

@keyframes showLove {
    0% {
        opacity: 1.0;
    }
    50% {
        opacity: 1.0;
    }
    100% {
        opacity: 0.0;
        top: -3rem;
    }
}

@media screen and (min-width: 960px) {
    #detailContainer {
        flex-direction: row;
        align-items: flex-start;
        margin: 0;
    }

    #detailTxtContent {
        margin-right: 2rem;
    }

    #detailImgWrapper {
        margin-right: 1rem;
    }
}

